.rsvp {
  width: 50%;
  padding: 40px;
  background: #fff;
  border-radius: 20px;
  text-align: center;
  height: 70vh;
  width: 50vw;
  position: absolute;
  box-shadow: 7px 9px 17px 3px #cc9b9b;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }
  .rsvp__curly-brace {
    width: 25vw;
    position: absolute;
    left: 50%;
    bottom: 10%;
    color: #e6a89c;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); }
  .rsvp__brace {
    border-bottom: 5px solid;
    margin: 0 10%;
    width: 30%;
    height: 20px;
    float: left; }
    .rsvp__brace:before, .rsvp__brace:after {
      content: "";
      width: 10%;
      height: 50px;
      position: absolute;
      display: block; }
    .rsvp__brace--left:before {
      border-bottom: 5px solid;
      border-bottom-left-radius: 20px;
      margin-left: -10%;
      margin-top: -30px; }
    .rsvp__brace--left:after {
      border-top: 5px solid;
      border-top-right-radius: 20px;
      margin-left: 30%;
      margin-top: 20px; }
    .rsvp__brace--right:before {
      border-top: 5px solid;
      border-top-left-radius: 20px;
      margin-left: -10%;
      margin-top: 20px; }
    .rsvp__brace--right:after {
      border-bottom: 5px solid;
      border-bottom-right-radius: 20px;
      margin-left: 30%;
      margin-top: -30px; }



